<script setup>
import { ref } from 'vue';
import axios from 'axios';

const studentId = ref('');
const expenseStatus = ref(null);
const errorMessage = ref(''); // ← 에러 메시지 상태 추가

const checkExpense = async () => {
  expenseStatus.value = null;
  errorMessage.value = '';

  try {
    const response = await axios.post('/api/expenses', {
      student_id: studentId.value,
    });

    if ('expenses' in response.data) {
      expenseStatus.value = response.data.expenses;
    } else {
      errorMessage.value = '등록되지 않은 학번입니다.';
    }
  } catch (err) {
    errorMessage.value = '등록되지 않은 학번입니다.';
    console.error(err);
  }
};

</script>

<template>
  <div class="student-check-container">
    <h2>자율적학생경비 확인 서비스</h2>

    <div class="input-group">
      <input
        v-model="studentId"
        type="text"
        placeholder="학번을 입력하세요"
        class="input"
      />
      <button @click="checkExpense" class="btn">조회하기</button>
    </div>

    <div class="status-message">
        <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
        <p v-else-if="expenseStatus !== null" :class="expenseStatus ? 'paid' : 'unpaid'">
            {{ expenseStatus ? '✅ 납부자입니다.' : '❌ 미납부자입니다.' }}
        </p>
    </div>

  </div>
</template>

<style scoped>
.student-check-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  background-color: #fafafa;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.input-group {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.input {
  flex: 1;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

.btn {
  background-color: #2c3e50;
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 6px;
  cursor: pointer;
}

.btn:hover {
  background-color: #1a242f;
}

.status-message {
  margin-top: 1rem;
  font-size: 1.1rem;
  font-weight: bold;
}

.paid {
  color: #27ae60;
}

.unpaid {
  color: #e74c3c;
}

.error {
  color: #e67e22;
}

</style>
